const axios = require('axios');
const APIURL = 'https://api.nytimes.com/svc';
const APIKEY = 'wLVTDbLpDOMB7Gylzrt667bQIRUqqyb0';

export const requestsMixin = {

    methods: {
        getArticles(section) {
            console.log('Getting articles...');
            return axios.get(`${APIURL}/topstories/v2/${section}.json?api-key=${APIKEY}`);
        },

        searchArticles(keyword) {
            console.log('Searching articles...');
            return axios.get(`${APIURL}/search/v2/articlesearch.json?api-key=${APIKEY}&q=${keyword}`);
        }
    }, // methods
};
